	import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
	import {UniRate} from 'components/uni/uni-rate/uni-rate'
	import {GetAllPaperType,GetPaperList} from 'api/exam'
  export default {
	components:{
		UniRate,
		UniLoadMore
	},
    data () {
      return {
        pageCount: 1,
        page: 0,
        classes: "未选择", //选中课程
        searchText: "", //搜索内容
        value: null,
        size: 9,
        readonly: true,
        list: [], //接口返回数组
        difficulty: [0], //难度星星
        allClass: [],
        active: null,
        id: null,
        typeData: [],
        typeId: 0,
        paperid: 0,
        categoryid: 0,
		moreStatus:"",
      };
    },
	/*下拉刷新*/
	onPullDownRefresh() {
		this.getList()	 
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			this.getList(this.page)	
		}	
	},
    methods: {

      // 跳转考试内容
      getExamList (url) {
        this.$router.push(url);
      },

      open () {
        this.$router.push({
          name: "examselect"
        });
      },
      newClass (classes) {
        this.classes = classes;
      },
      sidebar (id) {
        this.typeId = id;
        this.getList();
      },
	  getList(p=1){
	  	this.page = p
	  	if(this.page==1){
	  		this.list = []
	  	}
	  	this.moreStatus = 'loading'
	  	GetPaperList({
	  		categoryid: this.categoryid,
	  		typeid: this.typeId,
	  		page: p,
	  		pagesize: 15
	  	}).then(res=>{
	  		this.pageCount = res.pagecount
	  		if(this.page >= this.pageCount){
	  			this.moreStatus = 'noMore'
	  		}else{
	  			this.moreStatus = 'more'
	  		}
	  		if(res.result){
	  			this.list = this.list.concat(res.data)
				for (var i in this.list) {
				  //根据难度返回星星颗数
				  this.difficulty.push(
				    (this.list[i].paper_level == 0 && 1) ||
				    (this.list[i].paper_level == 1 && 2) ||
				    (this.list[i].paper_level == 2 && 3)
				  );
				}
	  		}
	  	})		
	  }
     
    },
    onLoad (query) {
		this.categoryid = query.categoryid
      this.$store.ready(state=> {
        // 获取用户课程记录
        if (uni.getStorageSync('classes_'+state.userInfo.UserID)) {
          this.classes = uni.getStorageSync('classes_'+state.userInfo.UserID);
        }
        //获取套卷练习分类名称
        GetAllPaperType().then(res=> {
          if (res.result) {
            this.typeData = res.data;
			
          }
		  this.getList();
        });
     
      })

    },
    onShow () {
      
    }
  };
